.header {
    position: fixed;
    top: 0;
left:0;
right:0;
    width: 100%;
    height: 5rem;
    background-color: #8a2b06;
    color: white;
    display: flex;
    justify-content: space-between;
  
    align-items: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
  }
  .header h1
  {
    margin-left: 5%;
  }
  .header button
  {
    margin-right: 5%;
  }
  .main-image {
    width: 100%;
    height: 25rem;
    z-index: 0;
    overflow: hidden;

  }
  
  .main-image img {
    width: 110%;
    height: 100%;
    object-fit: fill;
    
    transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
  }